.item{
    margin: 1rem 0;

}
.image{
    width: 100%;
    height: 20rem;
    overflow: hidden;
    border-top-right-radius: 6px;
    border-top-left-radius: 6px;
}

.image img{
    width: 100%;
    object-fit: cover;
}

.content{
    text-align: center;
    padding: 1rem;
}

.content h3{
    font-size: 1.25rem;
}

.actions{
   display: flex;
   align-items: center;
   justify-content: center;
   width: 100%;
}