.header{
    /* width: 100%; */
    /* height: 5rem; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(85, 85, 209);
    padding: 20px 03%;
    flex-wrap: wrap;

}

.logo{
    font-size: 1.5rem;
    color: white;
    font-weight: bold;
}

.header ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: baseline;
}

.header li{
    margin-left: 1rem;
}

.header a{
   text-decoration: none;
   font-size: 1rem;
   color: #bbb6ff;
}

.header a:hover,
.header a:active{
    color: white;
}

.badge{
    background-color: #0011ffea;
    color: white;
    border-radius: 12px;
    padding: 0  1rem;
    margin-left: 0.5rem;
}